/* styles for board menu */

.board-menu {
  position: absolute;
  right: 0;
  background: #ccc;
  padding: 1.5rem;
  top: 0;
  bottom: 0;
  width: 32%;
  min-width: 320px;
  max-width: 400px;
  -webkit-animation: boardMenuAnim 0.3s ease-in;
          animation: boardMenuAnim 0.3s ease-in;
}

.board-menu-list {
  margin-top: 1rem;
}

.board-menu-item {
  padding: 0.4rem 0.7rem;
  border: 1px solid #aaa;
  border-radius: 0.25rem;
  margin-bottom: .3rem;
  cursor: pointer;
}

.board-menu-item:hover {
  background: #aaa
}

@-webkit-keyframes boardMenuAnim {
  from {
    margin-right: -50px;
  }
  to {
    margin-right: 0px;
  }
}

@keyframes boardMenuAnim {
  from {
    margin-right: -50px;
  }
  to {
    margin-right: 0px;
  }
}
.modal {
  overflow-y: auto;
}

.add-board-modal-content {
  width: 30rem;
  margin-top: 3.5rem !important;
  background: transparent;
}

.add-board-modal-content input {
  background: rgba(0, 0, 0, 0.2);
}

.add-board-modal-content input:hover {
  background: rgba(0, 0, 0, 0.4);
}

.add-board-modal-content input:focus {
  background: rgba(0, 0, 0, 0.7);
  outline: none;
}

.details-modal-container {
  width: 70%;
  max-width: 750px;
  background: #f4f4f4;
  padding: 1rem;
  margin: 0 auto;
  margin-top: 3.3rem;
  border-radius: 0.2em;
  margin-bottom: 8rem;
}

.details-modal-header {
  padding-bottom: 1rem;
}

.details-modal-header .close {
  font-size: 2rem;
}

.details-modal-body {
  padding-top: 1rem;
  min-height: 64vh;
}

.fast-edit-modal-right {
  padding: 0.3rem 0.55rem;
  border-radius: 0.25rem;
  background: white;
}

.fast-edit-modal-right textarea {
  font-family: 'Roboto', sans-serif;
  height: 6rem !important;
  resize: none;
  font-size: 0.9rem;
}

.fast-edit-modal-right textarea:focus {
  outline: none;
}

.fast-edit-modal-action {
  display: block;
  margin-bottom: 0.2rem
}

.fast-edit-modal-action-btn {
  background: rgba(34, 34, 34, 0.8);
  padding: 0.5rem 1rem;
  display: inline-block;
  color: #f4f4f4;
  font-size: 0.9rem;
  border-radius: 0.2rem;
  cursor: pointer;
  -webkit-transition: 0.15s;
  transition: 0.15s;
}

.fast-edit-modal-action-btn:hover {
  margin-left: .3rem;
  background: rgba(34, 34, 34, 1);
}

.fast-edit-modal-action-container {
  -webkit-animation: actionsContainerAnim 100ms ease-in;
          animation: actionsContainerAnim 100ms ease-in;
  margin-left: 0;
}

/* optiosn modal */
#options-modal {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
  -webkit-transition: 0s;
  transition: 0s;
}

.options-modal-container {
  position: absolute;
  border-radius: 0.25rem;
  background: white;
  width: 300px;
  padding: 0.6rem;
  -webkit-transition: 0s;
  transition: 0s;
}

.options-modal-header {
  font-size: 0.95rem;
  border-bottom: 1px solid var(--light-color);
  padding: 0.7rem 0.5rem;
  padding-top: 0;
  text-align: center;
  cursor: default;
}

.options-modal-body {
  padding: 0.7rem;
}

.label-item {
  display: flex;
  flex-flow: row
}

.label-color {
  flex-grow: 1;
  margin-right: 0.4rem;
  vertical-align: middle;
  cursor: pointer;
  position: relative;
}

.label-color-check {
  position: absolute;
  top: 9px;
  right: 9px;
}

.label-color-text {
  position: absolute;
  top: 8px;
  left: 7px;
}

.label-color-green-hover {
  box-shadow: -8px 0 #519839;
}
.label-color-yellow-hover {
  box-shadow: -8px 0 #d9b51c;
}
.label-color-orange-hover {
  box-shadow: -8px 0 #cd8313;
}
.label-color-red-hover {
  box-shadow: -8px 0 #b04632;
}
.label-color-purple-hover {
  box-shadow: -8px 0 #89609e;
}
.label-color-blue-hover {
  box-shadow: -8px 0 #055a8c;
}
.label-color-sky-hover {
  box-shadow: -8px 0 #0098b7;
}
.label-color-lime-hover {
  box-shadow: -8px 0 #4bbf6b;
}
.label-color-pink-hover {
  box-shadow: -8px 0 #c9558f;
}
.label-color-black-hover {
  box-shadow: -8px 0 #091e42;
}

.color-for-select {
  width: 45px;
  height: 45px;
  margin: .15rem .2rem;
  display: inline-block;
  border-radius: .27rem;
  position: relative;
  cursor: pointer;
}

.color-for-select i {
  position: absolute;
  top: 16px;
  left: 17px;
}

.list-action-item {
  width: 100%;
  background: rgba(180, 255, 230, 0);
  padding: 0.5rem 0.3rem;
  cursor: pointer;
  margin-bottom: 0.2rem;
}

.list-action-item:hover {
  background: rgba(180, 180, 180, 0.137);
}

.item-choosen {
  background: rgba(180, 180, 180, 0.3);
  cursor: default
}

.item-choosen:hover {
  background: rgba(180, 180, 180, 0.3);
}

.delete-list:hover {
  color: var(--danger-color);
}

.move-list-modal select, .move-card-modal select {
  border: none;
  background: #e7f5eb;
}

.move-list-modal section, .move-card-modal section {
  padding: 0.25rem 1.2rem;;
  background: #e7f5eb;
  border-radius: 0.35rem;
  margin-bottom: 0.4rem;
}

.move-card-modal textarea {
  resize: none;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Noto Sans,Ubuntu,Droid Sans,Helvetica Neue,sans-serif;
  font-size: 0.9rem;
  margin-bottom: 0.7rem;
}

.details-label {
  width: 40px;
  height: 40px;
  margin: 0.2rem;
  border-radius: 0.2rem;
  cursor: pointer;
  position: relative;
  border: none !important;
}

.details-label:first-child {
  margin-left: 0 !important;
}

.details-label i {
  position: absolute;
  top: 9px;
  left: 10px;
  opacity: 0.7;
}

.details-desc {
  width: 100%;
  padding: 0.5rem;
  margin: 0.2rem;
  margin-top: 0.8rem;
  font-size: 0.9rem;
  background: #dee7e9;
  border-radius: 0.3rem;
  cursor: pointer;
  min-height: 4rem;
}

.details-desc:hover {
  background: #d7dadb;
}

.details-desc-compose-area {
  width: 100%;
  padding: 0.5rem;
  margin: 0.2rem;
  margin-top: 0.8rem;
  font-size: 0.9rem;
}

.details-desc-compose-area textarea {
  resize: none;
  font-size: 0.9rem;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Noto Sans,Ubuntu,Droid Sans,Helvetica Neue,sans-serif;
  padding: 0.3rem 0.5rem;
  margin-bottom: 0.5rem;
}

.details-right-panel > section > h5 {
  padding: .3rem;
  margin-bottom: 0.4rem;
}

.details-right-panel > section:last-child {
  margin-top: 1.5rem;
}

.details-right-panel > section > div {
  background: rgba(173, 181, 226, 0.308);
  cursor: pointer;
  padding: 0.4rem 0.4rem;
  margin-bottom: 0.3rem;
  border-radius: 0.25rem;
}

.details-right-panel > section > div:hover {
  background: rgba(136, 144, 180, 0.308);
}

.details-right-panel > section > div:active {
  background: rgba(41, 194, 255, 0.308);
}

.details-right-panel > section > div > i {
  margin-right: 0.45rem;
}

.watching-check {
  position: absolute;
  top: 0.25rem;
  right: 0;
}

.checklist-item {
  margin: 0.3rem;
  width: 99%;
  padding: 0.5rem 0.4rem;
  border-radius: 0.2rem;
  position: relative;
}

.checklist-item:hover {
  background: #dee7e9;
}

.checklist-item input {
  margin-left: -1.6rem !important; 
  margin-right: 1rem !important;
  border: 0;
}

.checklist-item .btn {
  position: absolute;
  top: 0.18rem;
  right: 0.1rem;
  display: none;
}

.checklist-item:hover .btn {
  display: block;
}

.progress-bar {
  height: .4rem;
  width: 100%;
  background: var(--light-color);
  border-radius: 10rem;
  margin-top: 1.2rem;
}

.progress {
  height: .4rem;
  border-radius: 10rem;
  background: var(--success-color);
}

@-webkit-keyframes actionsContainerAnim {
  from {
    margin-left: -1rem;
  }
  to {
    margin-left: 0;
  }
}

@keyframes actionsContainerAnim {
  from {
    margin-left: -1rem;
  }
  to {
    margin-left: 0;
  }
}



/* Global Styles */
:root {
  --primary-color: #388d6a;
  --dark-color: #333333;
  --light-color: #ddd;
  --danger-color: #da6671;
  --success-color: #55a368;
  --warning-color: #f7d87e;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

body {
  height: 100%;
  width: 100%;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Noto Sans,Ubuntu,Droid Sans,Helvetica Neue,sans-serif;
  font-size: 1rem;
  line-height: 1;
  background-color: #fff;
  color: #333;
  /* overflow: hidden; */
}

a {
  color: #388d6a;
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  color: #666;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

